.login-container {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #f5f5f5;
  background-image: -webkit-gradient(linear, 0 0, 0 100%, from(#1A2980), to(#26D0CE));
  background-image: -webkit-linear-gradient(top, #1A2980, #26D0CE);
  background-image: linear-gradient(top, #1A2980, #26D0CE);
  background-repeat: repeat-x;
}
.login-container .login-form {
  width: 328px;
  margin: 0 auto;
  text-align: left;
}
.login-container .App-logo {
  display: block;
  margin: 0 auto;
}

.index-content {
  position: relative;
  width: 100%;
}
.index-content .content .content-header {
  position: relative;
  width: 100%;
  height: 42px;
  line-height: 42px;
}
.index-content .content .content-body {
  position: relative;
  width: 100%;
}
.index-content .content .content-body .charts-cell {
  margin-right: 15px;
  border-radius: 8px;
  box-shadow: 3px 3px 3px #f0f0f0, -3px -3px 3px #f0f0f0;
  background: #fff;
}

.home-container .ant-layout-header {
  padding: 0 20px;
}
.home-container .logo {
  width: 156px;
  height: 32px;
  line-height: 32px;
  margin: 16px 24px 16px 0;
  float: left;
}
.home-container .logo img {
  width: 32px;
}
.home-container .logo.logoCollapsed {
  width: 40px;
}
.home-container .trigger {
  float: left;
  font-size: 18px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  color: #fff;
}
.home-container .trigger:hover {
  color: #1890ff;
}
.home-container .head-menu {
  float: left;
}

.container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}
.container-star {
  background-image: -webkit-linear-gradient(top, #292256 0%, #8446cf 70%, #a871d6 100%);
  background-image: linear-gradient(to bottom, #292256 0%, #8446cf 70%, #a871d6 100%);
}
.container-star:after {
  background: -webkit-radial-gradient(center, ellipse, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 40%, rgba(15, 10, 38, 0.2) 100%);
  background: radial-gradient(ellipse at center, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 40%, rgba(15, 10, 38, 0.2) 100%);
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
}
.star-1 {
  position: absolute;
  border-radius: 50%;
  background-color: #ffffff;
  -webkit-animation: twinkle 5s infinite ease-in-out;
  animation: twinkle 5s infinite ease-in-out;
}
.star-1:after {
  height: 100%;
  width: 100%;
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
  content: "";
  position: absolute;
  background-color: #fff;
  border-radius: 50%;
}
.star-1:before {
  background: -webkit-radial-gradient(center, ellipse, rgba(255, 255, 255, 0.5) 0%, rgba(255, 255, 255, 0) 60%, rgba(255, 255, 255, 0) 100%);
